var _this;
export default {
	data() {
		return {
			title:"",
			description:"",
			price: "",
			cycle: "",
			mobile: "",
			address: "",
			type: "",
			keywords: "",
			imgList: [],
			multiArray: [
				["湖北"],
				["武汉市"],
				["请选择","江岸区", "江汉区", "硚口区", "汉阳区", "武昌区", "青山区", "洪山区", "东西湖区", "汉南区", "蔡甸区", "江夏区", "黄陂区", "新洲区", "其它区"]
			],
			multiIndex: [0,0,0],
			canSubmit: false,
			loading: false
		}
	},
	mounted() {
		_this = this;
	},
	methods: {
		submitTask(){
			if(!_this.loading){
				_this.loading = true
				if (this.title.length == "") {
				     uni.showToast({
				        icon: 'none',
						position: 'bottom',
				        title: '标题必填'
				    });
				    return;
				}
				if (this.description.length == "") {
				     uni.showToast({
				        icon: 'none',
						position: 'bottom',
				        title: '描述必填'
				    });
				    return;
				}
				if (this.price.length == "") {
				     uni.showToast({
				        icon: 'none',
						position: 'bottom',
				        title: '价格必填'
				    });
				    return;
				}
				if (this.cycle.length == "") {
				     uni.showToast({
				        icon: 'none',
						position: 'bottom',
				        title: '周期必填'
				    });
				    return;
				}
				if (this.mobile.length == "") {
				     uni.showToast({
				        icon: 'none',
						position: 'bottom',
				        title: '联系电话必填'
				    });
				    return;
				}
				if (this.address.length == "") {
				     uni.showToast({
				        icon: 'none',
						position: 'bottom',
				        title: '地址必填'
				    });
				    return;
				}
				var _data = {
					title: _this.title,
					description: _this.description,
					price: _this.price,
					cycle: _this.cycle,
					mobile: _this.mobile,
					address: _this.multiArray[0][_this.multiIndex[0]]+" "+_this.multiArray[1][_this.multiIndex[1]]+" "+_this.multiArray[2][_this.multiIndex[2]]+ " " +_this.address,
					type: 0,
					keywords: "",
					imgList:_this.imgList
				}
				_this.$api.addTask(_data,res=>{
					_this.loading = false
					if(res.code == 0){//登陆成功
						uni.showToast({
						    title: '添加成功,待审核'
						});
					}else{
						uni.showToast({
						    title: '服务异常'
						});
					}
				})
			}
			
		},
		textareaBInput(e) {
			this.description = e.detail.value
		},
		MultiChange(e) {
			this.multiIndex = e.detail.value
		},
		ChooseImage() {
			_this.$api.uploadImage(4, res => {
				if (res.code==0) {
					_this.imgList.push(res.data)
				} else {
					that.$common.errorToShow("异常")
				}
			})
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '删除',
				content: '确定要删除吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
	}
}
